<template>
    <view class="page bgfb">
        <view style="margin-left: 20px;padding-top: 30px;">{{ $t('个') }}</view>
        <view style="padding-top: 0upx">
            <qiun-data-charts type="column" :opts="opts" :chartData="chartData" :ontouch="true" />
        </view>

        <view class="stoptitle">{{ $t('次品报表分析') }}</view>

        <view class="stopList">
            <view v-for="item in detailData.legend" :key="item.id">
                <view class="bcName">{{ item.name }}</view>
                <view class="ch-flex">
                    <view class="flex-item" v-for="bcItem in detailData.analyse[item.bcCode]" :key="bcItem">{{ bcItem.reason
                    }}：<text>{{
    bcItem.badQty }}</text>{{ $t('个') }}</view>
                </view>
            </view>
        </view>
    </view>
</template>

<script>
import Api from "@/api/bad/index.js"
export default {
    data() {
        return {
            isGet: true,
            chartData: {},
            detailData: {},
            listData: [],
            opts: {
                color: ["#91CB74", "#FF6600", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4", "#ea7ccc"],
                padding: [15, 15, 0, 5],
                rotate: false,
                touchMoveLimit: 24,
                enableScroll: true,
                legend: { show: true, position: 'top' },
                xAxis: {
                    disableGrid: true,
                    scrollShow: true,
                    itemCount: 5
                },
                yAxis: {
                    data: [
                        {
                            min: 0
                        }
                    ]
                },
                extra: {
                    column: {
                        type: "group",
                        width: 20,
                        activeBgColor: "#000000",
                        activeBgOpacity: 0.08
                    }
                }
            },
        };
    },

    onLoad(options) {
        uni.$u.http.get(Api.detail + "dispatchNo=" + options.dispatchNo + "&dispatchPrior=" + options.dispatchPrior).then(result => {
            console.log('result', result);
            if (result.code === 200) {
                this.detailData = result.data
                this.getServerData();
            }
        }).catch(er => {
            console.log('er', er);
        }).finally(() => {
            this.isGet = false
        })
    },

    onShow() {
        uni.setNavigationBarTitle({ // 修改头部标题
            title: this.$t("bad.record.detail")
        });
    },
    methods: {
        getServerData() {

            var titleList = []
            var seriesList = []
            var legendData = this.detailData.legend
            var analyseData = this.detailData.analyse
            legendData.forEach(item => {
                console.log("数据=", item)

                var bcList = []
                var titles = []
                var list = analyseData[item.bcCode]

                if (list != null) {

                    list.forEach(bcItem => {
                        bcList.push(parseInt(bcItem.badQty))
                        titles.push(bcItem.reason)
                    })

                    var vv = {
                        name: item.name,
                        data: bcList
                    }
                    seriesList.push(vv)
                    titleList = titles
                }
            });

            console.log("最后的数据=", seriesList)

            setTimeout(() => {
                let res = {
                    categories: titleList,
                    series: seriesList,
                };
                this.chartData = JSON.parse(JSON.stringify(res));
            }, 500);
        }
    }
}
</script>

<style lang="scss" scoped>
.bgfb {
    background-color: #fbfbfb;
}

.stoptitle {
    margin: 40upx 30upx 0;
    color: $ch-theme;
    padding-bottom: 20upx;
    font-size: 30upx;
    @include after($c: $ch-theme)
}

.stopList {
    margin: 40upx 30upx 0;

    .ch-flex {
        flex-wrap: wrap;
    }

    .flex-item {
        width: 50%;
        font-size: 28upx;
        color: #333;
        margin-bottom: 12upx;

        text {
            color: $ch-theme;
        }
    }
}

.bcName {
    color: $ch-theme;
    font-size: 30upx;
    margin-bottom: 20upx;
}
</style>
